@import url(https://netdna.bootstrapcdn.com/font-awesome/4.1.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    background: #ff564c;
    padding: 0;
    margin: 0;
    text-align: center;
    min-height: 100vh;
}

.screen {
    background-color: #ccc;
    padding: 0;
    height: 650px;
    width: 366px;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%, 0);
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.5);
    overflow: hidden;
    margin: 30px 0;
}

.songImage {
    background: url('https://lh3.googleusercontent.com/proxy/xZZRPbSbUJpQB8uIO0W6e8Pp7QIway7ExsryiN-GBm9Sgmc6mddvib0mBgVv540Z8uqYv2ikgBId7Y-IMa3V0qJvM9BO6tosNkT55wcsR8hFVoOExu3MUs1UlJCg6aEKX5bnjt-W') center no-repeat;
    background-size: cover;
    width: 366px;
    height: 366px;
}

.control-panel{
    position: relative;
    width: 100%;
    height: 286px;
    background: #111;
}

h2 {
    margin: 0;
    padding: 0;
    font-size: 10px;
    line-height: 10px;
    letter-spacing: 2.5px;
    font-family: 'Open Sans';
    font-weight: 700;
    color: #fff;
}

input[type=checkbox] {
    display: none;
    margin: 0;
    padding: 0;
    z-index: 10;
}

.bar-menu{
    position: absolute;
    top: 5px;
    right: 10px;
    font-size: 28px;
    color: #ff564c;
    cursor: pointer;
}

@media screen and (max-width: 320px){
    .App{
        max-height: 100vh;
    }
    .screen{
        margin: 0;
        height: 568px;
        width: 100%;
    }
    .songImage{
        width: 100%;
        height: 320px;
    }
    .control-panel{
        height: calc(100% - 320px);
    }
}

